import React from "react";
import styled from "styled-components";
import { AdminButton } from "../AdminButton";
import { FaBed, FaBath, FaHouseUser } from "react-icons/fa";
import { IoResize } from "react-icons/io5";
import { TbViewportWide } from "react-icons/tb";
import { getCustomColorsStyles } from "../../AppUI";
import { MdOtherHouses } from "react-icons/md";
import { TfiLayout } from "react-icons/tfi";

const Container = styled.div`
  bottom: 60px;
  left: 0px;
  right: 0px;
  position: absolute;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  visibility: ${({ open }) => (open ? "visible" : "hidden")};
`;

const Inner = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow-x: auto;
  white-space: nowrap;
  justify-content: end;
  align-items: center;
`;

const Inner2 = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow-x: auto;
  white-space: nowrap;
  justify-content: end;
  width: 100%;
  max-width: 600px;
`;

const Top = styled.div`
  border-radius: 5px;
  padding: 15px 20px;
  display: flex;
  flex-direction: column;
`;

const Details = styled.div`
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
`;

const NameContainer = styled.div`
  display: flex;
  flex-direction: column;
  min-width: 200px;
`;

const Name = styled.div`
  font-size: 40px;
`;

const Price = styled.div`
  font-size: 26px;
`;

const Availability = styled.div`
  font-size: 16px;
  text-transform: capitalize;
`;

const FacadeName = styled.div`
  margin-top: 20px;
  font-size: 16px;
`;

// const InfoContainer = styled.div`
//   display: flex;
//   flex-direction: column;
// `;

// const Frontage = styled.div`
//   line-height: 40px;
// `;

// const Depth = styled.div``;

const Stats = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-top: 20px;
`;

const StatContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 10px;
`;

const StatText = styled.div`
  margin-left: 10px;
`;

const Bottom = styled.div`
  border-radius: 5px;
  color: white;
  padding: 15px 20px;
  display: flex;
  margin-top: 5px;
  justify-content: center;
`;

const ButtonContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5px;
  margin: 0 10px;
  cursor: pointer;
  ${(props) => getCustomColorsStyles?.(props)};
`;

const ButtonText = styled.div`
  margin-top: 5px;
`;

const Stat = ({
  icon,
  admin,
  uiData,
  setUiData,
  name,
  hideHidden,
  hideControls,
  children,
}) =>
  uiData?.[name]?.hide && (hideHidden || !admin) ? null : (
    <div style={{ position: "relative" }}>
      <StatContainer>
        {icon}
        <StatText>{children}</StatText>
      </StatContainer>
      {admin && (
        <AdminButton
          uiData={uiData}
          setUiData={setUiData}
          name={name}
          button
          hideControls={hideControls}
        />
      )}
    </div>
  );

const Button = ({
  onClick,
  admin,
  uiData,
  setUiData,
  name,
  label,
  hideHidden,
  hideControls,
  icon,
}) =>
  uiData?.[name]?.hide && (hideHidden || !admin) ? null : (
    <div style={{ position: "relative" }}>
      <ButtonContainer onClick={onClick} uiData={uiData}>
        {icon}
        <ButtonText>{uiData?.[name]?.label || label}</ButtonText>
      </ButtonContainer>
      {admin && (
        <AdminButton
          uiData={uiData}
          setUiData={setUiData}
          name={name}
          button
          hideControls={hideControls}
        />
      )}
    </div>
  );

const AdminElem = ({
  admin,
  uiData,
  setUiData,
  name,
  children,
  hideHidden,
  hideControls,
}) =>
  uiData?.[name]?.hide && (hideHidden || !admin) ? null : (
    <div style={{ position: "relative" }}>
      {children}
      {admin && (
        <AdminButton
          uiData={uiData}
          setUiData={setUiData}
          name={name}
          button
          hideControls={hideControls}
        />
      )}
    </div>
  );

const ApartmentControls = (props) => {
  const { emitUIInteraction, setActiveUI, activeUI, chosenApratment, project } =
    props;
  const apartment = project?.properties.find((i) => i.name === chosenApratment);
  const isVisible = apartment && chosenApratment >= -1;
  return isVisible ? (
    <Container open={isVisible}>
      <Inner>
        <Inner2>
          <Top
            style={{
              backgroundColor: props.uiData?.background?.hex || "black",
            }}
          >
            <Details>
              <NameContainer>
                <AdminElem {...props} name="apartmentName">
                  <Name>{apartment.Name}</Name>
                </AdminElem>

                <AdminElem {...props} name="apartmentPrice">
                  <Price>{apartment.Price}</Price>
                </AdminElem>
                <AdminElem {...props} name="apartmentAvailability">
                  <Availability>{apartment.Availability}</Availability>
                </AdminElem>
                <AdminElem {...props} name="aprtmentFacadeName">
                  <FacadeName>{apartment.FacadeName}</FacadeName>
                </AdminElem>
              </NameContainer>
              {/* <InfoContainer>
                <Frontage>Lot frontage: 10m2</Frontage>
                <Depth>Lot depth: 10m2</Depth>
              </InfoContainer> */}
            </Details>
            <Stats>
              <AdminElem {...props} name="BedStat">
                <Stat {...props} name="BedStat" icon={<FaBed size={30} />}>
                  {`${apartment.BedroomsCount} beds`}
                </Stat>
              </AdminElem>
              <AdminElem {...props} name="BathStat">
                <Stat {...props} name="BathStat" icon={<FaBath size={30} />}>
                  {`${apartment.BathroomsCount} baths`}
                </Stat>
              </AdminElem>
              <AdminElem {...props} name="SurfaceStat">
                <Stat
                  {...props}
                  name="SurfaceStat"
                  icon={<IoResize size={30} />}
                >
                  {`${apartment.Surface} m2`}
                </Stat>
              </AdminElem>
              <AdminElem {...props} name="FrontageStat">
                <Stat
                  {...props}
                  name="FrontageStat"
                  icon={<TbViewportWide size={30} />}
                >
                  {`${apartment.Frontage} m`}
                </Stat>
              </AdminElem>
              <AdminElem {...props} name="DepthStat">
                <Stat
                  {...props}
                  name="DepthStat"
                  icon={
                    <TbViewportWide
                      size={30}
                      style={{ transform: "rotate(90deg)" }}
                    />
                  }
                >
                  {`${apartment.Depth} m`}
                </Stat>
              </AdminElem>
            </Stats>
          </Top>
          <Bottom
            style={{
              backgroundColor: props.uiData?.background?.hex || "black",
            }}
          >
            <Button
              {...props}
              icon={<FaHouseUser size={30} />}
              onClick={() => window.open(apartment.URLPlan, "_blank").focus()}
              label="3D Tour"
              name="TDTour"
            ></Button>
            <Button
              {...props}
              icon={<FaHouseUser size={30} />}
              onClick={() =>
                window.open(apartment.URLVirtualTour, "_blank").focus()
              }
              label="Virtual tour"
              name="VTour"
            ></Button>
            <Button
              {...props}
              icon={<TfiLayout size={30} />}
              onClick={() => setActiveUI([...activeUI, "floorPlan"])}
              label="Floor plan"
              name="FloorPlan"
            ></Button>
            <Button
              {...props}
              icon={<MdOtherHouses size={30} />}
              onClick={() =>
                window.open("https://www.realestate.com.au", "_blank").focus()
              }
              label="Property info"
              name="PropertyInfo"
            ></Button>
          </Bottom>
        </Inner2>
      </Inner>
    </Container>
  ) : null;
};

export { ApartmentControls };
